import React, { Component } from "react";
import './Time.css';


class Time extends Component {
  state = {
    date: new Date()
  };

  callMe() {
    setInterval(() => {
      this.setState({ date: new Date() });
    }, 1000);
  }

  render() {
    return (
      <div className="timeBlock">
        <h2 className="time"> Current Date and Time </h2>
        <p className="td"> {this.state.date.toDateString()}</p>
        <p className="td"> {this.state.date.toLocaleTimeString()}</p>
        {this.callMe()}
      </div>
    );
  }
}

export default Time;